<template>
  <div class="brandManage">

    <div class="brandManage-title">
      <span class="brandManage-title-slashBefor">汽车管理</span>
      <span class="brandManage-title-slash">/</span>
      <span class="brandManage-title-slashAfter">汽车品牌管理</span>
    </div>

    <!-- 表单 -->
    <div class="brandManage-form">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="formInline.name" placeholder="品牌名称"></el-input>
        </el-form-item>

        <el-form-item>
          <el-select v-model="formInline.country" placeholder="国家">
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item.title"
              :value="item.nation"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSearch" icon="el-icon-search">查询</el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="success" @click="openAddDialog" icon="el-icon-plus">新增</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <div class="brandManage-table">
      <el-table
        height="400"
        :data="pageList"
        :header-cell-style="{background:'rgb(238, 241, 246)',color:'rgb(96, 98, 102)'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center" header-align="center"></el-table-column>
        <el-table-column prop="name" label="品牌名称" align="center" header-align="center"></el-table-column>
        <el-table-column prop="country" label="国家" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.country == 'China'">中国</span>
            <span v-else-if="scope.row.country == 'Germany'">德国</span>
            <span v-else-if="scope.row.country == 'America'">美国</span>
            <span v-else-if="scope.row.country == 'France'">法国</span>
            <span v-else-if="scope.row.country == 'Italy'">意大利</span>
            <span v-else-if="scope.row.country == 'Japan'">日本</span>
            <span v-else-if="scope.row.country == 'Korea'">韩国</span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="company" label="所属公司" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.company"> {{scope.row.company}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="founder" label="创始人" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.founder"> {{scope.row.founder}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="time" label="创立时间" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.time">{{ scope.row.time.replace(/-/g, '年').replace(/(\d)$/, '$1月').replace(/年(\d)月$/, '年0$1月') }}</span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="200" align="center" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" @click="openEditBox(scope.row)" icon="el-icon-edit">编辑</el-button>
            <el-button
              @click="openDeleteBox(scope.row)"
              type="danger"
              size="small"
              icon="el-icon-delete"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="brandManage-pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="totalNumber"
      ></el-pagination>
    </div>

    <!-- 新增 -->
    <div class="brandManage-add">
      <el-dialog
        title="新增"
        :visible.sync="addDialog"
        width="30%"
        center
        :before-close="closeAddBox"
      >
        <div class="brandManage-add-form">
          <el-form
            :model="addForm"
            status-icon
            :rules="rules"
            ref="addForm"
            label-width="100px"
            class="demo-addForm"
          >
            <el-row :gutter="20">
              <el-col :span="24">
                <el-form-item label="品牌名称" prop="name">
                  <el-input v-model.trim="addForm.name" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="国家" prop="country">
                  <el-select v-model="addForm.country">
                    <el-option label="中国" value="China"></el-option>
                    <el-option label="德国" value="Germany"></el-option>
                    <el-option label="美国" value="America"></el-option>
                    <el-option label="法国" value="France"></el-option>
                    <el-option label="意大利" value="Italy"></el-option>
                    <el-option label="日本" value="Japan"></el-option>
                    <el-option label="韩国" value="Korea"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="所属公司" prop="company">
                  <el-input v-model="addForm.company"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="创始人" prop="founder">
                  <el-input v-model="addForm.founder"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="创立时间" prop="time">
                  <el-date-picker v-model="addForm.time" value-format="yyyy-MM" type="month" format="yyyy年MM月"></el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeAddBox">取 消</el-button>
          <el-button type="primary" @click="isAdd(addForm)">添 加</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 编辑框 -->
    <div class="brandManage-edit">
      <el-dialog
        title="编辑"
        :visible.sync="editDialog"
        width="30%"
        center
        :before-close="closeEditBox"
      >
        <div class="brandManage-add-form">
          <el-form
            :model="editForm"
            status-icon
            :rules="rules"
            ref="editForm"
            label-width="100px"
            class="demo-editForm"
          >
            <el-row :gutter="20">
              <el-col :span="24">
                <el-form-item label="品牌名称" prop="name">
                  <el-input v-model.trim="editForm.name" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="国家" prop="country">
                  <el-select v-model="editForm.country">
                    <el-option label="中国" value="China"></el-option>
                    <el-option label="德国" value="Germany"></el-option>
                    <el-option label="美国" value="America"></el-option>
                    <el-option label="法国" value="France"></el-option>
                    <el-option label="意大利" value="Italy"></el-option>
                    <el-option label="日本" value="Japan"></el-option>
                    <el-option label="韩国" value="Korea"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="所属公司" prop="company">
                  <el-input v-model="editForm.company"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="创始人" prop="founder">
                  <el-input v-model="editForm.founder"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="创立时间" prop="time">
                  <el-date-picker v-model="editForm.time" value-format="yyyy-MM" type="month" format="yyyy年MM月"></el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeEditBox">取 消</el-button>
          <el-button type="primary" @click="isConfirm">提 交</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { carBrand } from "../api/";

export default {
  name: "InformationManagement",
  data() {
    return {
      // 表单
      formInline: {
        name: "",
        country: ""
      },
      // 新增表单
      addForm: {
        name: "",
        country: "",
        company: "",
        founder: "",
        time: "",
      },
      editForm: {}, // 编辑表单
      countryList: [
        { title: "全部国家", nation: "all" },
        { title: "中国", nation: "China" },
        { title: "德国", nation: "Germany" },
        { title: "美国", nation: "America" },
        { title: "法国", nation: "France" },
        { title: "意大利", nation: "Italy" },
        { title: "日本", nation: "Japan" },
        { title: "韩国", nation: "Korea" },
      ],
      tableData: [],
      tableList: [],
      pageList: [],
      totalNumber: 0,
      addDialog: false,
      editDialog: false,
      currentPage: 1,
      pageSize: 10,
      rules: {
        name: { required: true, message: "请输入品牌名称", trigger: "blur" }
      }
    };
  },
  computed: {
    isCollapse() {
      return this.$store.state.isCollapse;
    }
  },
  created() {
    carBrand
      .then(res => {
        this.tableData = res.data.data;
        this.totalNumber = this.tableData.length;
        this.pageList = this.tableData.filter((item, index) => index < this.pageSize );
      })
      .catch(function(error) {
        return error;
      });
  },
  watch: {
    tableData () {
      this.onSearch()
    }
  },
  methods: {
    // 查询
    onSearch () {
      const name = this.formInline.name; // 品牌名称
      const country = this.formInline.country; // 国家
      const searchList = this.tableData.filter( value => {
        if (country && country != "all" && value.country != country) {
          return false
        }
        return true
      });

      const finalList = searchList.filter( value => {
        // 模糊查询
        if (name && !value.name.includes(name)) {
          return false
        }
        return true
      });
      this.pageList = finalList.filter((item, index) => index < this.pageSize );
      this.totalNumber = finalList.length;
    },

    // 分页
    handleCurrentChange(val) {
      this.pageList = this.tableData.filter((item, index) => index < this.pageSize * val && index >= this.pageSize * (val - 1))
      this.currentPage = val
    },

    // 新增
    //打开新增对话框
    openAddDialog() {
      this.addDialog = !this.addDialog;
    },
    // 关闭新增对话框
    closeAddBox() {
      this.addDialog = !this.addDialog;
    },
    // 确认添加吗
    isAdd() {
      this.$refs["addForm"].validate(valid => {
        if (valid) {
          this.$confirm("确认添加吗？")
            .then(_ => {
              let form = JSON.parse(JSON.stringify(this.addForm));
              this.tableData.unshift(form);
              this.closeAddBox();
              this.$message({
                type: "success",
                message: "添加成功!"
              });
            })
            .then(() => {
              this.$refs["addForm"].resetFields();
            })
            .catch(_ => {
              this.closeAddBox();
            });
        } else return false;
      });
    },

    // 编辑
    // 打开编辑框
    openEditBox(row) {
      this.editDialog = !this.editDialog;
      this.editForm = row;
      console.log(this.editForm.time)
    },
    // 关闭编辑框
    closeEditBox() {
      this.editDialog = !this.editDialog;
    },
    // 确认提交修改吗
    isConfirm(done) {
      this.$confirm("确认提交吗？")
        .then(_ => {
          done();
        })
        .catch(_ => {
          this.closeEditBox();
        });
    },

    // 删除
    // 打开删除框
    openDeleteBox(row) {
      this.$confirm("确认删除该记录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.handleClick(row);
        this.$message({
          type: "success",
          message: "删除成功!"
        });
      });
    },
    // 删除元素
    handleClick(row) {
      const id = row.id;
      const deleteValue = this.tableData.filter(row => id == row.id);
      this.tableData.splice(this.tableData.indexOf(deleteValue[0]), 1);
    }
  }
};
</script>

<style scoped>
.brandManage {
  padding: 20px;
  padding-top: 10px;
}
/* 标题 */
.brandManage-title {
  padding-bottom: 10px;
}
.brandManage-title-slashAfter,
.brandManage-title-slashBefor {
  font-size: 16px;
  color: #606266;
}
.brandManage-title-slashAfter {
  font-weight: bold;
}
.brandManage-title-slash {
  margin: 0 5px;
  font-size: 16px;
  font-weight: bold;
  color: #c0c4cc;
}
/* 表单 */
.brandManage-form {
  padding: 10px;
  position: relative;
  background-color: #f4f4f4;
}
.el-form-item {
  margin-bottom: 0;
}
/* 表格 */
.brandManage-table {
  margin: 10px 0;
}
/* 分页 */
.brandManage-pagination {
  text-align: center;
  background-color: #f4f4f4;
}
/* 新增框 */
.brandManage-eadd .el-col {
  margin: 10px 0;
}
/* 编辑框 */
.el-col {
  margin: 8px 0;
}
</style>
<style>
.is-without-controls .el-input__inner{
  text-align: left;
}
</style>


